<template>
  <div id="app">
    <InstallPWA
      v-if="visibleInstallPwaModal"
      @install="install"
    />
    
    <IFrame
      v-if="serviceWorkerLoaded && !visibleInstallPwaModal"
      :url="url"
    />
  </div>
</template>

<script>

import { mapActions, mapGetters } from 'vuex'
import InstallPWA from '@/components/InstallPWA.vue'
import IFrame from '@/components/IFrame.vue'
import CheckPwaMixin from '@/mixins/CheckPwaMixin'

export default {
  name: 'Main',
  components: {
    InstallPWA,
    IFrame
  },

  data: () => ({
    // serviceWorkerLoaded: localStorage.serviceWorkerLoaded || false,
    visibleInstallPwaModal: false,
    installed: localStorage.installed || false,
    deferredPrompt: localStorage.deferredPrompt || null,
    userLang: '',
    url: '',
    subid: ''
  }),

  mixins: [CheckPwaMixin],

  computed: {
    ...mapGetters({
      // notificationPermission: 'app/notificationPermission',
      // isAuthenticated: 'auth/isAuthenticated',
      // userLanguage: 'user/language',
      platform: 'app/platform',
      // browser: 'app/browser',
      isPwa: 'app/isPwa',
      serviceWorkerLoaded: 'app/serviceWorkerLoaded'
    })
  },

  methods: {
    ...mapActions({
      // getNotificationPermission: 'app/getNotificationPermission',
      // setProfile: 'user/setProfile',
      setPlatform: 'app/setPlatform',
      // setBrowser: 'app/setBrowser'
    }),

    install () {
      if (this.platform === 'mobile' && this.browser === 'chrome') {
        if (this.isPwa && this.isPwaInstalling) {
          // this.$store.commit('app/SET_SNACKBAR_MESSAGE', this.$t('snackbar.app_installing'))
          console.log('snackbar.app_installing')
        } else if (this.isPwa) {
          this.promptInstall()
        } else if (!this.serviceWorkerLoaded) {
          // this.$store.commit('app/SET_SNACKBAR_MESSAGE', this.$t('app_loading'))
          console.log('app_loading')
        } else {
          // window.open(`${process.env.VUE_APP_FERZ_URL}/app`, '_blank')
          console.log('else')
        }
      }
    }

    // async install() {
    //   console.log('yes')
    //   console.log(this.deferredPrompt)
    //   console.log(localStorage.deferredPrompt)
    //   this.deferredPrompt.prompt()
    //   this.installed = true
    // }
  },

  mounted () {
    const isPwa = navigator.standalone || matchMedia('(display-mode: standalone)').matches
    console.log(isPwa)

    if (this.platform === 'ios' && !isPwa && process.env.NODE_ENV === 'production') {
      this.visibleInstallPwaModal = true
    }

    const url = new URLSearchParams(window.location.search)
    this.subid = url.get('UTM')
    this.url = this.userLang === 'pt-BR' ? `https://go.affilirise.com/redirect.aspx?pid=2517&bid=1534&subid=${this.subid}` : 'https://cazimbosocial.com/'
    
  },

  watch: {
    serviceWorkerLoaded (val) {
      console.log(val)
      if (val) {
        console.log(navigator)
        navigator.serviceWorker.ready.then(() => {
          setTimeout(() => {
            if (this.platform === 'mobile' && this.isPwa) {
              console.log('go')
              this.visibleInstallPwaModal = true
            }
          }, 10000)
          console.log('Service Worker is ready')
        })
      }
    }
    // deferredPrompt(val) {
    //   localStorage.deferredPrompt = this.deferredPrompt
    // }
  },

  created () {
    this.setPlatform(this.Platform)
    // window.addEventListener("beforeinstallprompt", e => {
    //   e.preventDefault();
    //   // Stash the event so it can be triggered later.
    //   this.deferredPrompt = e;
    //   localStorage.deferredPrompt = e;
    //   console.log(this.deferredPrompt)
    //   console.log(localStorage.deferredPrompt)
    // })

    // window.addEventListener("appinstalled", () => {
    //   localStorage.deferredPrompt = null;
    //   localStorage.installed = true;
    // })

    if (localStorage.installed) {
      this.installed = localStorage.installed
    }

    if (localStorage.deferredPrompt) {
      this.deferredPrompt = localStorage.deferredPrompt
    }

    if (localStorage.userLang) {
      this.userLang = localStorage.userLang
    } else {
      localStorage.userLang = navigator.language || navigator.userLanguage
      this.userLang = localStorage.userLang
    }

  }
}
</script>

<style lang="scss">
#app {
  height: 100%;
}
</style>
